.photos{
	position: absolute;
	z-index: 1; 
	top: 15%; 
	left: 0%; 
	right: 0%; 
	width: 90%; 
	margin: auto; 
	background-color: white; 
	height: 60vh;
	border-radius: 10px; 
	box-shadow: 5px 8px 5px 1px;
}

@media screen and (min-width: 1001px) {
	.photos{
		position: absolute; 
		top: 15%; 
		left: 0%; 
		right: 0%; 
		width: 50%; 
		margin: auto; 
		background-color: white; 
		height: 70vh;
		border-radius: 10px; 
		box-shadow: 5px 8px 5px 1px;
	}
}