.App {
  text-align: center;
  background-color: rgb(237,237,237);
  width: 100%;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

button.btn.btn-light{
  min-width: 50px;
  position: relative;
  left: 5px;
  top: 18px;
}

#siteName{
  padding: 0px;
  margin-left: 5px;
  font-size: 18px;
  color: white;
}

canvas{
  position: absolute;
  top: 11vh;
  left: 0px;
  z-index: 0;
  max-height: 88vh;
 }

#basic-navbar-nav.navbar-collapse.collapse{
  left: 0%;
  right: 0%;
}

footer{
  position: fixed;
  overflow: hidden;
  z-index: 1;
  background-color: white; 
  margin:auto; 
  bottom: 0px;
  width: 90%; 
  left:0%; 
  right:0%; 
  text-align:center;  
  border-radius: 10px;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  box-shadow: 5px 8px 5px 1px;
} 

.navbar{
  width: 100%;
  min-height: 11vh;
}

#navButtons{
  position: absolute;
  display: block;
  top: 10%;
  margin-left: 0px;
  padding: 0px;
}

@media screen and (min-width: 401px) {
  .App{
    height: 80vh;
  }
  #navButtons{
    position: relative;
    display: inline-block;
    top: 0px;
  }
  .navbar{
    min-height: 120px;
  }
  footer{
    width: 100%;
  }
  .footNm{
    width: 100%;
    bottom: 0;
  }
  #tsparticles{
    display: block;
    height: 76vh;
  }
}

@media screen and (min-width: 1001px) {
  .App{
    height: 100vh;
  }
  button.btn.btn-light{
    min-width: 50px;
    position: relative;
    left: 5px;
    top: 18px;
  }
  .navbar{
    text-align: top;
    height: 10%;
  }
  footer{
    width: 100%;
  }
  .footNm{
    width: 100%;
    bottom: 0;
  }
  #tsparticles{
    display: block;
    height:87vh;
  }
}

